    right: 0;
    bottom: 0;
    left: 0;
  }
  .popup__overlay {
    width: 100%;
    height: 100%;
  }
  .popup__content {
    box-sizing: border-box;
  }
  .popup__content_full {
    overflow-y: auto;
    width: 100%;
